



























import ut from '../../utils/index.js';
export default {
	data() {
		return {
			adressinf:{
				"address": "",
				"cityCode": "11",
				"cityName": "北京",
				"countyAreaCode": "12",
				"countyAreaName": "天安门",
				"defaultType": 0,
				"name": "",
				"phone": "",
				"provinceCode": "22",
				"provinceName": "撒旦"
			},
			id:''
		}
	},
	onLoad(opt) {
		if(opt._id){
			this.id=opt._id;
			this.req_addressInf(opt._id);
		}
	},
	methods: {
		cg_active(){
			if(this.adressinf.defaultType){
				this.adressinf.defaultType=0;
				return;
			}
			this.adressinf.defaultType=1;
		},
		req_addaddress(){
			let url="address/add";
			if(this.id){
				url="address/update"
			}
			ut.request({
				data:this.adressinf,
				url: url,
				c:true
			}).then(data=>{
				wx.navigateBack()
			})
		},
		req_addressInf(id){
			ut.request({
				data:{
				id: id
				},
				url: "address/detail"
			}).then(data => {
				this.adressinf=data;
			})
		}
	}
}



